/************************************************************ 1 General
                                                              Style

******************************************************************************************************************************************************/
html {
  overflow-x: hidden;
}
body {
  margin: 0;
  padding: 0;
  font-family: Arial !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/assets/images/body-bg.jpg");
  background-repeat: repeat;
  font-size: 12px !important;
  overflow-x: hidden;
}

a,
body {
  color: #575757 !important;
}

.title h2 {
  font-size: 24px;
  color: #363636;
  margin-left: 15px;
  margin-right: 7px;
  font-weight: 600;
  text-align: center;
  padding: 0 0 6px;
  font-family: "Source Sans Pro", sans-serif;
}

input:focus,
button:active:focus {
  outline: 0;
  box-shadow: none !important;
}

.mobile-header .react-ripples {
  display: block !important;
}

/* .modal {
  top: 100px;
} */

.rc-collapse-header:focus {
  outline: none;
}

img:focus {
  outline: none;
}

.black {
  color: black;
}

.body-bg {
  pointer-events: none;
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.loader-style {
  width: 100%;
  height: 50vh;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pro-container .loader-style {
  height: 100%;
}

.bg-orange {
  background: #f8810c;
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #fcd72b;
  width: 120px;
  height: 120px; /* Safari */
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/***********  Spinner **********/

@keyframes spinner__effect--8-child-1 {
  50% {
    transform: translate(-325%, -325%);
  }
}

@keyframes spinner__effect--8-child-2 {
  50% {
    transform: translate(0, -325%);
  }
}

@keyframes spinner__effect--8-child-3 {
  50% {
    transform: translate(325%, -325%);
  }
}

@keyframes spinner__effect--8-child-4 {
  50% {
    transform: translate(325%, 0);
  }
}

@keyframes spinner__effect--8-child-5 {
  50% {
    transform: translate(325%, 325%);
  }
}

@keyframes spinner__effect--8-child-6 {
  50% {
    transform: translate(0, 325%);
  }
}

@keyframes spinner__effect--8-child-7 {
  50% {
    transform: translate(-325%, 325%);
  }
}

@keyframes spinner__effect--8-child-8 {
  50% {
    transform: translate(-325%, 0);
  }
}

@keyframes spinner__effect--8-child-9 {
  50% {
    transform: scale(2);
  }
}

.spinner__container {
  display: flex;
  width: 100%;
  height: 399px;
  align-items: center;
  justify-content: center;
  padding: 30px;
  position: absolute;
  z-index: 11;
}

.spinner__dot {
  width: 15px;
  height: 15px;
  margin: 0 10px;
  border-radius: 100%;
}

.spinner__square {
  width: 10px;
  height: 10px;
}

.spinner__effect--8 .spinner {
  position: relative;
  transform: rotate(45deg);
}

.spinner__effect--8 .spinner .spinner__square {
  position: absolute;
  background: #fff;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  list-style: none;
}

.spinner__effect--8 .spinner .spinner__square:nth-child(1) {
  animation-name: spinner__effect--8-child-1;
  animation-delay: calc(0.1s * 1);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(2) {
  animation-name: spinner__effect--8-child-2;
  animation-delay: calc(0.1s * 2);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(3) {
  animation-name: spinner__effect--8-child-3;
  animation-delay: calc(0.1s * 3);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(4) {
  animation-name: spinner__effect--8-child-4;
  animation-delay: calc(0.1s * 4);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(5) {
  animation-name: spinner__effect--8-child-5;
  animation-delay: calc(0.1s * 5);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(6) {
  animation-name: spinner__effect--8-child-6;
  animation-delay: calc(0.1s * 6);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(7) {
  animation-name: spinner__effect--8-child-7;
  animation-delay: calc(0.1s * 7);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(8) {
  animation-name: spinner__effect--8-child-8;
  animation-delay: calc(0.1s * 8);
}

.spinner__effect--8 .spinner .spinner__square:nth-child(9) {
  animation-name: spinner__effect--8-child-9;
  animation-delay: 1.25s;
}

.headroom.headroom--pinned.headroom--scrolled {
  z-index: 111;
}

@font-face {
  font-family: bnb-font;
  src: url("/assets/css/fc_icon.woff");
}

.content-container {
  padding-top: 40px;
}

@media (max-width: 600px) {
  .embed-responsive {
    height: 92vh;
  }
}

@media (max-width: 800px) {
  .mobile-header {
    display: block;
  }
  .desktop-header {
    display: none;
  }
}
@media (min-width: 801px) {
  .mobile-header {
    display: none;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1260px !important;
  }
}
