/************************************************************ 2 COMPONENT
                                                             Header Style

******************************************************************************************************************************************************/

header {
  background: white;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
}

.order {
  width: 165px;
  background-color: #fff;
  border-radius: 3px;
  border: 1px solid #cecece;
  height: 36px;
  line-height: 37px;
  margin-top: 2px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.cart-icon {
  background-image: url("data:image/gif;base64,");
  background-repeat: no-repeat;
  float: left;
  height: 23px;
  width: 26px;
  overflow: hidden;
  background-color: transparent;
  position: relative;
}

.cart-count {
  color: #fff;
  font-size: 11px;
  height: 17px;
  position: absolute;
  right: 0;
  top: 0;
  width: 19px;
  line-height: 16px;
  overflow: hidden;
  text-align: center;
}

.btn-order:hover {
  background: gray none repeat scroll 0% 0% !important;
}
.btn-order:focus {
  box-shadow: none;
}

.btn-order:not(:disabled):not(.disabled):active {
  background: gray none repeat scroll 0% 0% !important;
}

.btn-order {
  font-size: 12px;
  color: #fff !important;
  /* background: gray none repeat scroll 0% 0%; */
  background: rgb(255, 142, 43) none repeat scroll 0% 0% !important;
  border: 0 !important;
  line-height: 22px;
  font-weight: bold;
  padding: 4px 12px;
}

.header-container .form-inline {
  position: relative;
}
.header-container .form-control {
  height: 36px;
  font-size: 11px;
  font-weight: 600;
  color: #676767;
}

.header-container .form-control:focus {
  border: 1px solid #85be3c !important;
  box-shadow: 0 0 3px rgba(133, 190, 60, 0.5), 0 1px 0 rgba(0, 0, 0, 0.07) inset;
  transition: all 0.3s ease-in;
}

.header-container .form-inline img {
  position: absolute;
  right: 15px;
}

.dropdown-menu .tab-content {
  height: 345px;
}
.navbar-bg.navbar.navbar-expand-lg.navbar-light.bg-light.sticky-top {
  background-color: transparent !important;
}

@media (min-width: 601px) and (max-width: 992px) {
  .header-container .form-inline img {
    background: white;
    padding-left: 6px;
  }
}

/************************************************************ 2.1 Media Queries
                                                              Header Style Responsive 

******************************************************************************************************************************************************/

@media (min-width: 1000px) {
  .header-container .form-control {
    width: 600px;
  }
}
@media (max-width: 600px) {
  .header-container .form-inline {
    width: 100%;
  }
  .header-container {
    flex-direction: column;
  }
  .order {
    width: 100%;
    justify-content: space-between;
    padding: 0 14px;
  }
}

/************************************************************ 2.2
                                                              Navbar Style

******************************************************************************************************************************************************/

.navbar-bg {
  transition: all 0.3s ease-in;
  background-color: transparent !important;
  background-image: url("/assets/images/link-bg.png");
  background-repeat: repeat-x;
  position: relative;
  height: 58px;
  z-index: 7;
}

.nav-link.line,
#basic-navbar-nav .dropdown-toggle.nav-link {
  color: #3a3a3a !important;
  text-align: center;
  font-size: 12px;
  line-height: 1.3;
  text-transform: uppercase;
  font-weight: 500;
  padding: 0 16px !important;
  display: flex;
  align-items: center;
  height: 58px;
  /* width: 88px; */
  width: auto;
}

.line::after,
#basic-navbar-nav .dropdown-toggle.nav-link::after {
  content: "|";
  left: 12px;
  background: #c1f00000;
  position: relative;
  top: -3px;
  font-size: 25px;
  color: #e6cb32;
  width: 0px;
  border: 0;
  margin: 0;
}

.nav-link:hover,
.nav-link:active {
  background: white;
}

/************************************************************ 2.3
                                                              Top Bar Style

******************************************************************************************************************************************************/

.topbar {
  background-color: #fff;
  height: 20px;
  text-align: right;
  padding-right: 20px;
  padding-top: 5px;
}

.topbar a {
  color: #7f7f7f;
}
.topbar span:first-child {
  border-left: 0;
}

.topbar span {
  padding-right: 6px;
  padding-left: 6px;
  border-left: 1px solid #e6e6e6;
  height: 10px;
  cursor: pointer;
  color: #7f7f7f;
  font-size: 11px;
  font-weight: 400;
}

.topbar .dropdown {
  display: inline-flex;
}

#account-dropdown {
  background: white;
  padding-right: 6px;
  padding-left: 6px;
  border: 0;
  border-left: 1px solid #e6e6e6 !important;
  height: 10px;
  cursor: pointer;
  color: #7f7f7f;
  font-size: 11px;
  font-weight: 400;
  padding-top: 0;
}

.topbar .dropdown:focus {
  outline: 0 !important;
  box-shadow: none !important;
}

.topbar .dropdown .dropdown-menu {
  transform: translate3d(-60px, 20px, 0px) !important;
  border: 1px solid #ddd !important;
  box-shadow: 3px 2px 3px #888 !important;
  padding: 0;
  margin: 0;
}

.topbar .dropdown-item {
  font-size: 11px;
  color: #7f7f7f;
  height: 20px;
  line-height: 20px;
  text-align: left;
  padding: 0 10px;
}

.topbar .dropdown-item:hover {
  background: #ececec;
}

.topbar .dropdown-menu.show::before {
  content: "";
  width: 8px;
  height: 8px;
  position: absolute;
  top: -5px;
  left: 75px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  transform: rotateZ(45deg);
  background: white;
  right: 0;
  margin: auto;
}
/************************************************************ 2.4
                                                              Drop Down Style

******************************************************************************************************************************************************/

.navbar-nav .dropdown-menu {
  visibility: hidden;
  transition-delay: 0.1s;
  transition-duration: 0.2s;
  display: block;
}

.navbar-nav .dropdown:hover .dropdown-menu {
  transition-delay: 0.3s !important;
  visibility: visible !important;
  /* display: block; */
}

.navbar-bg.activeclick .dropdown:hover .dropdown-menu {
  visibility: hidden !important;
  /* display: block; */
}

.dropdown.show {
  background: white;
}

.dropdown .dropdown-menu {
  width: 100%;
  width: 100%;
  top: 50px;
  border-radius: 0;
  border: 0;
}

#basic-navbar-nav .dropdown:hover .dropdown-toggle::after {
  display: none;
}

#basic-navbar-nav .dropdown.show:active .dropdown-toggle::after {
  display: none;
}

/************************************************************ 2.5
                                                              Tab Inside DropDown Style

******************************************************************************************************************************************************/

.inner-tab {
  color: white;
  background-color: #eee;
  height: 38px;
  box-shadow: inset 0 -3px 10px -7px #555;
  margin: -4px 4px 0;
  padding: 0 60px;
}

.tab-content .main {
  padding: 10px 60px;
}
.inner-tab.navbar-nav {
  padding-left: 60px;
}
.inner-tab .nav-link-tab {
  height: 38px;
  color: black;
  position: relative;
  font-size: 12px;
  padding: 10px 18px !important;
}

.inner-tab .nav-link-tab.active {
  background: black;
  color: white !important;
}

.inner-tab .nav-link-tab.active:hover {
  color: white;
}

.inner-tab .nav-link-tab.active::after {
  content: "";
  border-color: #000 #0000;
  border-style: solid;
  border-width: 8px 8px 0 8px;
  width: 0;
  height: 0;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -8px;
}

.underline {
  font-size: 12px;
  border-bottom: 1px solid #eee;
  line-height: 10px;
  cursor: pointer;
  clear: both;
  width: auto;
  display: inline-block;
  margin: 0 20px 0 0;
}

.underline.featured {
  font-size: 14px;
  font-weight: 700;
  color: #3b3b3b !important;
  border: none;
  line-height: 14px;
}

.underline.featured:hover {
  text-decoration: underline;
}

.underline:nth-child(13) {
  margin-top: 29px;
}

.tab-content .title {
  font-weight: 700;
  font-size: 14px;
  color: #3b3b3b;
}

.col-border {
  border-right: 1px solid #eee !important;
  max-height: 330px;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  flex-basis: 100%;
}

.col-border .new {
  background-color: #e11923;
  color: #fff;
  font-size: 11px;
  font-style: italic;
  font-weight: 700;
  margin-left: 5px;
  padding: 2px 5px;
}

/************************************************************ 2.6
                                                              Top Brand Inside DropDown Tab Style 

******************************************************************************************************************************************************/

.top-brand {
  overflow: hidden;
  padding-left: 60px;
  height: 35px;
  font-size: 13px;
  background-color: #ffec86;
  align-items: center;
  margin: 0 4px;
}

.top-brand label {
  font-weight: 700;
  color: #000;
  margin: 0;
}

.top-brand .nav-link {
  float: left;
  border-right: 1px solid #e7d363;
  height: 17px;
  padding-right: 6px !important;
  margin-left: 7px;
  line-height: 0px;
  color: #333;
}

.top-brand .nav-link:hover {
  color: #000;
  background: transparent;
}

/************************************************************ 2.7
