/************************************************************ 3 COMPONENT
                                                            Slider Style

******************************************************************************************************************************************************/
.slick-slider.home-slider {
  margin-top: -4px;
  height: 399px;
  border-bottom: 4px solid #fff;
}

.slide {
  display: flex !important;
  justify-content: center;
}

.slick-prev,
.slick-next {
  width: 43px;
  height: 37px;
}
.slick-prev {
  left: 20px;
  z-index: 1;
}
.slick-next {
  right: 20px;
}

.slick-next::before,
.slick-prev::before {
  font-size: 40px;
  text-shadow: -5px 0px 0px white;
}

@media (max-width: 600px) {
  .slick-slider.home-slider {
    height: 141px;
  }

  .slick-slider.home-slider .slick-slide img {
    width: 100%;
    height: auto;
  }
  .topbar {
    margin-bottom: 20px;
    text-align: center;
  }
  .top-brands-txt {
    top: 1px;
  }
  .big-store-img img {
    width: 90%;
    margin-top: 5px;
  }
}

@media (min-width: 601px) and (max-width: 992px) {
  .top-brands-txt {
    top: 0px;
  }
  #home-bottom .slick-slide img {
    height: 58px !important;
  }
}

