  text-align: center;
  display: inline-block;
}
#new-boutique .disc .disc-type {
  font-size: 14px;
  margin-top: 26px;
}

#new-boutique .disc .disc-price {
  font-size: 15px;
  margin-top: 2px;
  position: relative;
  top: -7px;
}

#new-boutique .pro-container {
  margin-top: 14px;
}

#new-boutique .pro-container:hover .pro-item {
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 4px 1px #aaa;
  position: absolute;
