  line-height: 236px;
}

#new-boutique .pro-container .hvslideshow {
  height: 199px;
}

#new-boutique .pro-container .pro-hvdetail .pro-loyality {
  display: none;
}

#new-boutique .pro-hvdetail .pro-action-btns {
  justify-content: center;
  background: #fff;
  margin-bottom: 10px;
