  background: #e8e8e8;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 40px;
}
.track-process .circle::before {
  content: "";
  width: 36px;
  height: 36px;
  position: absolute;
  display: block;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  text-align: center;
}
.track-process > div {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}
.track-more span {
  color: #03a9f4;
  cursor: pointer;
}
.track-details thead {
  background: #e8e8e8;
}
.track-more.active {
  display: none;
}
/*************** Loading Animation ****************/

body,
html,
.codewrapper {
  width: 100%;
  height: 100%;
}
.codewrapper {
  position: relative;
  background: #e7f0f7;
}
.codeprogressbar {
  display: block;
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 400px;
  height: 30px;
}
.codeprogressbar::before,
.codeprogressbar::after {
  content: "";
  display: block;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background-image: linear-gradient(
    90deg,
    #e91e63 0%,
    #e91e63 20%,
    #03a9f4 20%,
    #03a9f4 40%,
    #8bc34a 40%,
    #8bc34a 60%,
    #ffeb3b 60%,
    #ffeb3b 80%,
    #ff5722 80%,
    #ff5722 100%
  );
  background-position: 0 0;
  background-repeat: repeat-x;
  animation: movebar 5s linear infinite;
}
.codeprogressbar::before {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
}
.codeprogressbar::after {
  z-index: 9;
  top: 6px;
  -webkit-filter: blur(16px);
  filter: blur(16px);
  opacity: 0.7;
}
.codestylization {
  position: absolute;
  z-index: 999;
  height: 4px;
  width: 90%;
  left: 5%;
  top: 6px;
  opacity: 0.3;
}
.codestylization::before,
.codestylization::after {
  content: "";
  display: block;
  position: absolute;
  height: 4px;
  top: 0;
  border-radius: 2px;
}
.codestylization::before {
  background: #fff;
  left: 0;
  right: 10px;
}
.codestylization::after {
  width: 6px;
  background: #fff;
  right: 0;
}

.active-color,
.active-size {
  border: 1px solid black !important;
}

.active-age {
  border: 2px solid black !important;
}

.cursor-pointer {
  cursor: pointer;
}
@keyframes movebar {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 400px 0;
  }
}

